:root {
  --color_grey: #838383;
  --color__azure: #3896ab;
}

@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v25-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/montserrat-v25-cyrillic_latin-500.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v25-cyrillic_latin-600.woff2") format("woff2"), url("../fonts/montserrat-v25-cyrillic_latin-600.ttf") format("truetype");
}
/*===== global styles =====*/
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  color: #000;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

body.lock {
  overflow: hidden;
}

body.noscroll {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1520px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none !important;
}

button,
input {
  font-family: inherit;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
  color: #000;
}

button {
  cursor: pointer;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.visualhidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(0 0 99.9% 99.9%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  padding: 0;
  border: 0;
}

.hidescrollbar {
  scrollbar-width: none;
}
.hidescrollbar::-webkit-scrollbar {
  -webkit-appearance: none;
  display: none;
  width: 0;
  height: 0;
}

.btn__black {
  display: block;
  width: 100%;
  padding: 0;
  font-size: 17px;
  font-weight: 600;
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
  transition: color 0.2s ease-out, background-color 0.2s ease-out;
}
@media (hover: hover) {
  .btn__black:hover {
    color: #000;
    background-color: #fff;
  }
}

.btn__white {
  display: block;
  width: 100%;
  padding: 18px 20px;
  font-size: 17px;
  font-weight: 600;
  color: #000;
  background-color: #fff;
  border: 1px solid #000;
  transition: color 0.2s ease-out, background-color 0.2s ease-out;
}
@media (hover: hover) {
  .btn__white:hover {
    color: #fff;
    background-color: #000;
  }
}

.showmore__btn {
  display: none;
  width: 100%;
  max-width: 466px;
  justify-content: center;
  align-items: center;
  margin: 48px auto 0;
  padding: 20px 30px;
  text-align: center;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
  transition: all 0.2s ease-out;
}
@media (hover: hover) {
  .showmore__btn:hover {
    color: #000;
    background-color: #fff;
  }
}
@media (max-width: 767px) {
  .showmore__btn {
    display: flex;
  }
}
@media (max-width: 575px) {
  .showmore__btn {
    max-width: 506px;
  }
}

.sticker__sale {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 2;
  padding: 8px 14px;
  text-align: center;
  font-size: 17px;
  font-weight: 600;
  letter-spacing: 0.85px;
  color: #fff;
}

.sticker__blue {
  background-color: #001bc6;
}

.sticker__green {
  background-color: #65d80d;
}

.stiker__azure {
  background-color: #2e849a;
}

.sticker__red {
  background-color: #e84851;
}

.sticker__darkRed {
  background-color: #9c0029;
}

.stiker__hidden {
  display: none;
}

.goodsSection {
  content-visibility: auto;
  margin-top: 100px;
}
@media (max-width: 991px) {
  .goodsSection {
    margin-top: 160px;
  }
}

.goodsSection__title {
  padding: 31px 0 35px;
  color: #000;
  font-size: 32px;
  font-weight: 600;
  letter-spacing: 2.24px;
  text-transform: uppercase;
}
@media (max-width: 575px) {
  .goodsSection__title {
    font-size: 24px;
  }
}

.goodsCard__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0 24px;
}
@media (max-width: 991px) {
  .goodsCard__wrapper {
    grid-template-columns: repeat(2, 1fr);
    gap: 48px;
    padding: 0 80px;
  }
}
@media (max-width: 767px) {
  .goodsCard__wrapper {
    gap: 30px 20px;
    padding: 0;
  }
}
@media (max-width: 575px) {
  .goodsCard__wrapper {
    max-width: 335px;
    margin: 0 auto;
    grid-template-columns: 1fr;
    gap: 38px 0;
    padding: 0;
  }
}

.goodsCard__item {
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
}

.goodsCard__descr {
  width: 100%;
}

.goodsCard__img {
  position: relative;
  width: 100%;
  margin-bottom: 24px;
  border: 1px solid #d9d9d9;
}
.goodsCard__img::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 100%;
}
.goodsCard__img picture img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.goodsCard__addToFavorites {
  position: absolute;
  top: 13px;
  right: 23px;
  z-index: 3;
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.goodsCard__addToFavorites img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: opacity 0.2s ease-out;
}
.goodsCard__addToFavorites img:nth-child(1) {
  opacity: 1;
}
.goodsCard__addToFavorites img:nth-child(2), .goodsCard__addToFavorites img:nth-child(3) {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.goodsCard__addToFavorites:hover img:nth-child(1) {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.goodsCard__addToFavorites:hover img:nth-child(2) {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.goodsCard__addToFavorites:hover img:nth-child(3) {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.goodsCard__descr.addedToFavorites .goodsCard__addToFavorites img:nth-child(1) {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.goodsCard__descr.addedToFavorites .goodsCard__addToFavorites img:nth-child(2) {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.goodsCard__descr.addedToFavorites .goodsCard__addToFavorites img:nth-child(3) {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.goodsCard__price {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 5px 25px;
  margin-bottom: 21px;
}
.goodsCard__price span {
  font-size: 28px;
  font-weight: 600;
  letter-spacing: 1.4px;
  white-space: nowrap;
}
@media (max-width: 767px) {
  .goodsCard__price {
    margin-bottom: 24px;
  }
}

.goods__priceNew {
  color: #000;
}

.goods__priceOld {
  color: #acacac;
  text-decoration: line-through;
}

.goodsCard__name {
  width: 100%;
  margin-bottom: 11px;
  color: #000;
  font-size: 20px;
  font-weight: 600;
  line-height: 120%;
}

.goodsCard__about {
  width: 100%;
  margin-bottom: 28px;
  color: var(--color_grey, #838383);
  font-size: 15px;
  font-weight: 500;
  line-height: 146.667%;
}
@media (max-width: 767px) {
  .goodsCard__about {
    margin-bottom: 26px;
  }
}

.goodsCard__addToCart {
  align-self: flex-end;
  display: block;
  width: 100%;
  padding: 17px 30px;
  color: #000;
  text-align: center;
  font-size: 17px;
  font-weight: 600;
  border: 1px solid #d0d0d0;
  transition: all 0.2s ease-out;
}
.goodsCard__addToCart:hover {
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
}

.goodsCard__nav {
  position: absolute;
  top: 143px;
  left: 0;
  z-index: 2;
  width: 100%;
}
@media (max-width: 1300px) {
  .goodsCard__nav {
    top: 100px;
  }
}
@media (max-width: 767px) {
  .goodsCard__nav {
    display: none;
  }
}
@media (max-width: 991px) {
  .goodsCard__nav {
    top: 48%;
  }
}

.goodsCard__nav-wrapper {
  width: 100%;
  max-width: 1520px;
  min-width: 320px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  pointer-events: none;
}
@media (max-width: 991px) {
  .goodsCard__nav-wrapper .intro__prev {
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    border-right: 1px solid #000;
  }
  .goodsCard__nav-wrapper .intro__next {
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    border-left: 1px solid #000;
  }
}
@media (max-width: 767px) {
  .goodsCard__nav-wrapper {
    padding: 0 20px;
  }
}

[class*=copyrights-pane] {
  display: none !important;
}

.goodsSection .intro__prev,
.goodsSection .intro__next,
.discountedProducts .intro__prev,
.discountedProducts .intro__next {
  transition: opacity 0.2s ease-out;
}
.goodsSection .intro__prev.disabled,
.goodsSection .intro__next.disabled,
.discountedProducts .intro__prev.disabled,
.discountedProducts .intro__next.disabled {
  pointer-events: none;
}
.goodsSection.slider-start .intro__prev,
.discountedProducts.slider-start .intro__prev {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.goodsSection.slider-finish .intro__next,
.discountedProducts.slider-finish .intro__next {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.goodsCard__sliderWrapper {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  overflow: hidden;
}

.goodsCard__slide {
  position: relative;
  height: 100%;
  display: grid;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
  background-color: #fff;
  overflow: hidden;
}
.goodsCard__slide .goodsCard__inner {
  height: 100%;
  display: grid;
}
.goodsCard__slide:not(:first-child) {
  left: 100%;
}
.goodsCard__slide:not(:first-child) .goodsCard__inner {
  transform: translateX(-90%);
}

.input-required.error {
  border-color: #f00;
}
.input-required.error::placeholder {
  color: #f00;
}

@media (max-width: 575px) {
  .hidden-on-mobile {
    display: none;
  }
}

@keyframes fadeup {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }
  40% {
    transform: translateY(20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fadeleft {
  0% {
    transform: translateX(-20px);
    opacity: 0;
  }
  40% {
    transform: translateX(-30px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes faderight {
  0% {
    transform: translateX(30px);
    opacity: 0;
  }
  40% {
    transform: translateX(30px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes fadeupSecondLine {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }
  50% {
    transform: translateY(20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.header {
  position: relative;
  z-index: 10;
}

body.lock .header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.header__container {
  position: relative;
  z-index: 10;
  min-height: 46px;
  background-color: #000;
}
@media (max-width: 1300px) {
  .header__container .container {
    padding: 0 20px;
  }
}

.header__menu {
  position: relative;
  padding: 0 20px;
}
.header__menu ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0 20px;
  padding: 0 86px;
  overflow-x: auto;
}
.header__menu li {
  flex-shrink: 0;
  color: var(--color_grey, #838383);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
}
.header__menu a {
  display: inline-block;
  padding: 14px 0;
  transition: color 0.2s ease-out;
}
.header__menu a:hover {
  color: white;
}
@media (max-width: 1300px) {
  .header__menu {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 0;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
  }
  .header__menu ul {
    display: block;
    padding: 13px 61px 35px;
    background-color: #000;
  }
  .header__menu li:not(:last-child) {
    margin-bottom: 28px;
  }
  .header__menu a {
    padding: 0;
  }
}

.header__btn {
  position: relative;
  display: none;
  justify-content: flex-start;
  align-items: center;
  gap: 0 17px;
  padding: 12px 20px 11px 27px;
  color: var(--color_grey, #838383);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
  cursor: pointer;
}
.header__btn,
.header__btn svg,
.header__btn path {
  transition: all 0.2s ease-out;
}
.header__btn:hover {
  color: white;
}
.header__btn:hover path {
  fill: white;
}
.active .header__btn svg {
  transform: scaleY(-1);
}
@media (max-width: 1300px) {
  .header__btn {
    display: flex;
    padding-left: 0;
  }
}

.header__overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
  background-color: transparent;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.2s ease-out;
  cursor: pointer;
}
.header__overlay.show {
