.order {
  width: 100%;
  // overflow: hidden;

  @include xs {
    padding-bottom: 108px;
  }
}

.order__container {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 24px 0;

  @include lg {
    gap: 24px 0;
  }

  @include md {
    display: block;
    // overflow: hidden;
  }

  @include xs {
    overflow: hidden;
  }
}

.order__info {
  // flex-grow: 1;
  // flex-shrink: 1;
  flex: 1 1 100%;
}

.order__checkout {
  flex: 0 0 367px;
}

// order__info

.order__info {
  margin-bottom: 68px;

  @include xs {
    margin-bottom: 79px;
  }

  @include xxs {
    margin-bottom: 70px;
  }
}

.order__step {
  // height: 0;
  // max-height: 0;
  visibility: visible;
  // transition: all 0.2s ease-out;
  // overflow: hidden;

  @include xs {
    position: relative;
    border-bottom: none;

    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        left: -20px;
        right: -20px;
        bottom: 0;
        height: 1px;
        background-color: #d9d9d9;
      }
    }
  }

  // &.step-first {
  //   // height: auto;
  //   // max-height: none;
  // }

  &.hidden {
    // content-visibility: auto;
    // max-height: 0;
    height: 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  &.collapse {
    border-bottom: none;

    // &:not(:last-child) {
    //   &::after {
    //     display: none;
    //   }
    // }

    .order__step-content {
      // height: 0;
      // // max-height: 0;
      // visibility: hidden;
      pointer-events: none;
      border-bottom: none;

      .choices {
        pointer-events: none;
      }
    }
  }
}

.order__step-content {
  border-bottom: 1px solid #d9d9d9;

  // overflow: hidden;
  // transition: max-height 0.2s ease-out;
}

.order__heading {
  padding: 26px 0 21px;
  color: #000;
  font-size: 28px;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
  border-bottom: 1px solid #d9d9d9;

  span:first-child {
    margin-right: 54px;
    color: #acacac;
  }

  @include xs {
    padding: 30px 0 30px;
    font-size: 17px;
    letter-spacing: normal;

    span:first-child {
      margin-right: 32px;
    }
  }

  .collapse & {
    color: #acacac;
  }
}

.order__section {
  padding-right: 27px;
  padding-bottom: 28px;
  // border-bottom: 1px solid #d9d9d9;

  @include md {
    padding-right: 0;
  }
}

.order__step--map {
  .order__section {
    border-bottom: none;
  }

  @include xs {
    .order__section {
      padding-bottom: 20px;
    }
  }
}

.order__step--payment {
  // display: none;
  // // .order__step-content {
  // //   display: none;
  // // }
  .order__section {
    border-bottom: none;
  }

  @include xs {
    padding-bottom: 0;
    .order__section {
      padding-bottom: 0;
    }
  }
}

.order__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 23px 63px;
  padding: 30px 0 23px;

  @include xs {
    gap: 28px 63px;
    padding: 30px 0 27px;
  }

  @include xxs {
    flex-wrap: wrap;
    gap: 28px 0;
  }
}

.order__step--payment {
  .order__list {
    padding-bottom: 0;

    @include xs {
      padding-bottom: 23px;
    }
  }
}

.order__item {
  @include xxs {
    width: 100%;
  }
}

.order__label {
  position: relative;
  padding-left: 36px;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    z-index: 1;
    transform: translateY(-50%);
    width: 24px;
    height: 24px;
    background-color: #eee;
    border-radius: 50%;
  }

  span:nth-child(2) {
    position: absolute;
    left: 5px;
    top: 5px;
    z-index: 2;
    transform: rotate(-45deg);
    width: 13px;
    height: 7px;
    border-left: 2px solid #000;
    border-bottom: 2px solid #000;
    opacity: 0;
  }

  @include xs {
    font-size: 15px;
  }
}

.order__radio:checked + span {
  opacity: 1;
}

.order__inputs-row {
  // display: flex;
  // justify-content: space-between;
  // align-items: center;
  // flex-wrap: wrap;
  display: grid;
  // grid-template-columns: repeat(auto-fit, minmax(334px, 1fr));
  // grid-template-columns: repeat(3, 1fr);
  // grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-template-columns: repeat(3, 1fr);

  gap: 16px 23px;

  &:not(:last-child) {
    margin-bottom: 16px;
  }

  // @include lg {
  //   grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  // }

  @include lg {
    max-width: 600px;
    margin: 0 auto;
    grid-template-columns: 1fr;
  }

  // @include xxs {
  //   grid-template-columns: 1fr;
  // }
}

.order__input {
  // flex: 1 1 50%;
  padding: 14px 23px;
  color: #000;
  background-color: #f1f1f1;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  border: 1px solid #f1f1f1;

  &::placeholder {
    color: #757575;
  }
}

// input:-webkit-autofill,
// input:-webkit-autofill:hover,
// input:-webkit-autofill:focus {
//   background-color: rgba(241, 241, 241, 1) !important;
// }

.order__nav {
  padding-top: 28px;
  padding-right: 27px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px 23px;

  @include md {
    padding-right: 0;
  }

  @include xs {
    padding-top: 26px;
  }

  @include xxs {
    flex-wrap: wrap;
    gap: 16px 0;
  }
}

.order__prev,
.order__next {
  width: max-content;
  min-width: 220px;
  padding: 17px 20px;
  font-size: 17px;

  @include xxs {
    width: 100%;
  }
}

.first-step-is-current .order__prev,
.last-step-is-current .order__next {
  visibility: hidden;
  pointer-events: none;
  height: 0;
  padding: 0;
  // display: none;
}

@include xxs {
  .order__prev {
    order: 2;
  }

  .order__next {
    order: 1;
  }
}

.order__note {
  color: #838383;
  font-size: 16px;
  font-weight: 500;
  line-height: 160%;

  @include xs {
    font-size: 13px;
  }
}

// order__checkout

.order__checkout {
  border-left: 1px solid #d9d9d9;
  padding-bottom: 100px;

  @include md {
    max-width: 500px;
    margin: 24px auto 54px;
    padding-bottom: 0;
    border: none;
  }

  @include xs {
    position: relative;
    margin-top: 0;
    padding-top: 23px;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: -20px;
      right: -20px;
      height: 1px;
      background-color: #d9d9d9;
    }
  }
}

.order__promocode {
  margin-bottom: 45px;

  @include xs {
    margin-bottom: 40px;
  }
}

.order__checkout-list {
  margin-bottom: 31px;

  li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    line-height: 22px;

    &:not(:last-child) {
      margin-bottom: 17px;
    }

    span:first-child {
      color: #757575;
      font-weight: 500;
    }

    span:last-child {
      color: #000;
      text-align: right;
      font-weight: 600;
    }
  }
}

.order__msg {
  display: block;
  margin-bottom: 24px;
  padding: 16px;
  color: #fff;
  background-color: #757575;
  font-size: 13px;
  font-weight: 500;
  line-height: 140%;
}

.order-placement-page .cart__total {
  @include xs {
    margin-bottom: 41px;
  }
}
