.mapSection {
  content-visibility: auto;
  position: relative;
  margin-top: 265px;
  overflow: hidden;

  @include xl {
    margin-top: 202px;
  }

  @include sm {
    margin-top: 162px;
  }
}

.map__autoscroll {
  @include xs {
    border-bottom: none;
  }
}

.map {
  position: relative;
  min-height: 662px;

  @include xxs {
    min-height: 575px;
  }
}

.mapSection__container {
  position: relative;
  z-index: 2;
  width: 100%;
  max-width: 1920px;
  margin: 44px auto 0;
  padding-top: 26px;
  padding-bottom: 45px;
  pointer-events: none;
  // padding-top: 45px;
  // border-top: 4px solid #000;

  @include xs {
    margin-top: 0;
    padding-top: 0;
    border-top: none;
    min-width: 320px;
  }
}

.map__info {
  position: relative;
  // top: 70px;
  left: 240px;
  z-index: 3;
  width: 100%;
  max-width: 342px;
  pointer-events: none;
  // margin-top: 26px;

  @include xs {
    position: relative;
    top: unset;
    left: unset;
    max-width: 442px;
    margin: 0 auto;
    padding: 0 40px;
    // border-bottom: 1px solid #d9d9d9;
  }

  @include xxs {
    max-width: 402px;
    padding: 0 20px;
  }

  .choices {
    width: 100%;
    margin: 0 auto;
    pointer-events: all;
  }

  .choices__inner {
    font-size: 15px;
    font-weight: 600;
    color: #fff;
    background-color: #000;
    border: none;
    border-radius: unset;
    padding: 14px 30px 14px 26px !important;
  }

  .choices[data-type*="select-one"]:after {
    border-color: #fff transparent transparent;
  }

  .choices[data-type*="select-one"].is-open:after {
    border-color: transparent transparent #fff;
  }

  .choices__list--dropdown {
    border: 2px solid #000;

    .choices__item {
      padding: 10px 26px;
    }
  }
}

// store description

.map__description {
  width: 100%;
  margin-top: 24px;
  padding: 27px 26px 20px;
  border: 2px solid #000;
  background-color: #fff;
  pointer-events: all;
  transition: all 0.2s ease-out;

  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  @include xs {
    min-height: 530px;
    max-width: 362px;
  }

  &.show {
    visibility: visible;
    opacity: 1;
    pointer-events: all;
  }
}

.map__storeName {
  padding: 10px 0 32px;
  font-size: 24px;
  font-weight: 600;
  line-height: 91.667%;
  text-transform: uppercase;
}

.map__data {
  padding: 24px 0 20px;
  border-top: 1px solid #d9d9d9;

  &:last-child {
    padding-bottom: 22px;
  }
}

.map__name {
  margin-bottom: 10px;
  color: var(--color_grey, #838383);
  font-size: 12px;
  font-weight: 500;

  @include xs {
    margin-bottom: 7px;
  }
}

.map__value {
  font-size: 16px;
  font-weight: 600;
}

.map__storeName {
  font-size: 24px;
  font-weight: 600;
  line-height: 91.667%;
  text-transform: uppercase;
  color: #000;
}

.map__choose {
  margin-bottom: 10px;
  padding: 14px 20px;

  span {
    text-align: center;
  }

  span:nth-child(2) {
    display: none;
  }
}

.map__description.checked {
  .map__choose {
    background-color: #2dab17;
    border-color: #2dab17;

    span:nth-child(1) {
      display: none;
    }

    span:nth-child(2) {
      display: block;
    }

    &:hover {
      background-color: #fff;
    }
  }
}

.map__close {
  width: 100%;
  padding: 14px 20px;
}

.mapSection__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

// map on the order-placement-page

.order-placement-page {
  .mapSection__container {
    margin-top: 0;

    @include xs {
      min-width: auto;
    }
  }

  .map__info {
    left: 0;

    @include xs {
      max-width: 362px;
      padding: 0;
    }
  }

  .mapSection__wrapper {
    width: 80vw;
    left: unset;
    right: 0;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #d9d9d9;
    }

    &::before {
      top: -1px;
    }

    &::after {
      bottom: -1px;
    }

    @include md {
      width: unset;
      left: -40px;
      right: -40px;
    }

    @include xs {
      top: 53px;
      height: unset;
      bottom: 0;
      left: -20px;
      right: -20px;
    }
  }
}

// balloon for contacts page

.balloon-root {
  position: relative;
  width: 304px;
  min-height: 56px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  text-align: center;
  color: white;
  z-index: 10;
}

.close {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 5px;
  top: 3;
}

.balloon-body {
  width: 100%;
  height: 100%;
}

.balloon-pin {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 36px;
  background-color: #000;
}
